<template>
    <div class="block">
        <!-- <h2>{{language=='Chinese'?'':'After-Course Test'}}</h2> -->
        <h2></h2>
        <!-- <div class="divider"></div> -->

            <!-- <Card
                v-for = "(q,index) in questions"
                :key = "index"
                :class = "index"
                :question='questions[index]'
                :answers='answers[index]' 
                :correct='corrects[index]' 
                @next='handleNext'
            /> -->
            <Card
                v-if = this.isTest
                class='card'
                :percentage='percentage'
                :question='questions[this.index]'
                :answers='answers[this.index]' 
                :correct='corrects[this.index]' 
                :description='descriptions[this.index]'
                @next='handleNext'
                @result='handleResult'
            />

            <resultCard
                v-if = !this.isTest
                :know='alreadyKnow'
                :unknow='dontKnow'
                :correct='correctNum'
                @check = "drawer=true"
            />
        <!---------------------------------------------------------------->
        <!-- <transition name='el-zoom-in-center'>
            <Card
                v-show='show1' 
                :question='questions[1]'
                :answers='answers[1]' 
                :correct='corrects[1]' 
                @next='handleNext'
            />
        </transition> -->
        <el-drawer
            title=""
            :visible.sync="drawer"
            :with-header="false"
            size = "40%">
            

            <div style='margin:40px'>
                <checkCard
                    v-for = "(q,index) in questions"
                    :key = "index"
                    :question='questions[index]'
                    :answers='answers[index]' 
                    :correct='corrects[index]' 
                    :myAnswers ='myAnswers[index]'
                />
                
            </div>
        </el-drawer>

</div>
</template>

<script>
import Card from './components/Card';
import resultCard from './components/resultCard';
import checkCard from './components/checkCard';


export default {
    components:{
        Card,
        resultCard,
        checkCard,
    },
    data() {
      return {
        correctNum:0,
        alreadyKnow:[],
        dontKnow:[],
        drawer:false,
        isTest:true,
        percentage:10,
        know:[],
        index:0,
                corrects:[
                    2,1,3,1,2,3,0,2,2,1
                ],
        questions:[
            '1.',
            '2.',
            '3.sleep()wait()',
            '4.SpringBean',
            '5.Spring',
            `6.JSPpageJava!java.rmi.*!javautil.*`,
            '7.JSPJSP',
            '8.JSP__',
            '9.DBDBSDBMS ',
            '10.'
        ],
        answers:[
            //
            ['A. Double',
             'B. char',
             'C. String ',
             'D. double'
            ],
            //
            ['A. static',
             'B. final',
             'C. abstract',
             'D. No modifier can be used'
            ],
            //
            ['A. sleepThreadwaitObject',
             'B. sleepwait',
             'C. sleep',
             'D. waitnotify'
            ],
            //
            [
                'A. Bean',
                'B.',
                'C.',
                'D.constructor-index',
            ],
            //
            [
                'A. Spring',
                'B.SpringJDBCJDBC',
                'C.Spring',
                'D.Springchecked',
            ],
            //
            [
                'A. % page Language=Java, import=java.rmi.;java.uitl. %>',
                'B. % page language=Java Import=java.rmi.,java.uitl. %>',
                'C. % page language=Java; import=java.rmi.;java.uitl. %>',
                'D. % page language=Java import=java.rmi.* ,java.uitl.* %>',
            ],
            //
            [
                'A. forward',
                'B. include',
                'C. useBean',
                'D. setProperty',
            ],
            //
            [
                'A. <!>',
                'B. <%%>',
                'C. <%%>',
                'D. <%!%>',
            ],
            //
            [
                'A. DBDBSDBMS',
                'B. DBMSDBDBS',
                'C. DBSDBDBMS',
                'D. DBMSDBDBS',
            ],
            //
            [
                'A. ',
                'B. ',
                'C. ',
                'D. ',
            ],
        ],
        descriptions:[

           [
            '1:',
            `byteintcharlongfloatdoublebooleanshort `
            ],

            [
             '2:',
              `static (,,)
               final ()
               final static , `
            ],

            [
             '3:',
             'Notify'
            ],

            [
              '4:',
              `SpringSpringBean
              
              beanconstructor-argtype
              index `
            ],

            [
             '5:',
             `SpringSpringDAO
             JDBCHibernateJDOTopLink
             JdbcTemplateSpringJDBCRDBMSJDBCSpringDataAccessException
             
             RuntimeException`
            ],

            [
             '6:',
             `page<%@ page 1=1 2=2 %> 
             `
            ],

            [
             '7:',
             `<jsp:forward>jsp`
            ],

            [
             '8:',
              `JSP//    /*   */ <%--  --%>JSP<!> `
           
            ],

            [
             '9:',
             `5C`
            ],

            [
             '10:',
             `
             B`
            ]
        
        ],
        
        
        myAnswers:[],//myAnswer
      };
    },
    methods:{
        handleNext(prop,myAnswer){
            //this['show'+this.index]=false;
            this.know[this.index] = prop;
            this.myAnswers[this.index] = myAnswer;
                this.index += 1;
                this.percentage+=10;
            
            
            //this['show'+this.index]=true;
            // $('.card').slideUp("1500",()=>{
            //     this.index += 1;
            //     $('.card').slideDown("1500")
            // });
        },
        handleResult(prop,myAnswer){
            this.know[this.index] = prop;
            this.myAnswers[this.index] = myAnswer;
            console.log(this.know);
            console.log(this.myAnswers);
            let countSE = 0 , countStruct = 0 , countWeb = 0, countBase = 0;
            this.know.forEach( (element,index) => {
                if (element)this.correctNum+=1;

                if(index<3){
                    if(element){
                        countSE++
                    }
                }

                if(index>=3 && index <5){
                    if(element){
                        countStruct++
                    }
                }

                if(index >= 5 && index<8){
                    if(element){
                        countWeb ++
                    }
                }

                if(index >= 8 && index<10){
                    if(element){
                        countBase ++
                    }
                }


            });

            let storeKnow = [false,false,false,false]
            if(countSE>=2){
                storeKnow[0] = true
                this.alreadyKnow.push('JavaSE')
            }else{
                this.dontKnow.push('JavaSE')
            }
            if(countWeb>=2){
                storeKnow[1] = true
                this.alreadyKnow.push('JavaWeb')
            }else{
                this.dontKnow.push('JavaWeb')
            }
            if(countStruct>=1){
                storeKnow[2] = true
                this.alreadyKnow.push('Java ')
            }else{
                this.dontKnow.push('Java ')
            }
            if(countBase>=1){
                storeKnow[3] = true
                this.alreadyKnow.push('')
            }else{
                this.dontKnow.push('')
            }
            

            this.isTest = false;
            this.$store.commit('changeKnow',storeKnow)
        }
    },
    mounted(){
        this.index = 0;
    },
    computed:{
        language:function(){
            return this.$store.state.language
        }
    }
  };
</script>

<style>
.block{
    text-align: left;
    margin-bottom: 200px;
}
.block h2{
        width:fit-content;
        display: inline-block;
        padding:2px 12px 4px;
        border-bottom: 4px #FF4F4F solid;
    }
.radio{
    margin-top:5%;
    margin-bottom: 5%;
    font-family: Microsoft JhengHei;
}
.divider{
        background-color: #FF4F4F;
        height: 4px;
        width: 230px;
        margin-top: -18px;
    }
.block .activity-content{
    font-size:14px;
    line-height:24px;
    margin-bottom:-20px;
}

.el-drawer__body {
    overflow: auto;
    /* overflow-x: auto; */
}

/*2.*/
.el-drawer__container ::-webkit-scrollbar{
    display: none;
}
</style>


